.bubbles {
  width: 100%;
  height: 100%;
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.bubble {
  opacity: .5;
  background: #f1f1f1;
  border-radius: 50%;
  animation: flying 10s ease-in infinite;
  position: absolute;
  bottom: -75%;
}

.bubble:nth-child(1) {
  width: 40px;
  height: 40px;
  animation-duration: 8s;
  left: 10%;
}

.bubble:nth-child(2) {
  width: 20px;
  height: 20px;
  animation-duration: 5s;
  animation-delay: 1s;
  left: 20%;
}

.bubble:nth-child(3) {
  width: 50px;
  height: 50px;
  animation-duration: 10s;
  animation-delay: 2s;
  left: 35%;
}

.bubble:nth-child(4) {
  width: 80px;
  height: 80px;
  animation-duration: 7s;
  animation-delay: 0s;
  left: 50%;
}

.bubble:nth-child(5) {
  width: 35px;
  height: 35px;
  animation-duration: 6s;
  animation-delay: 1s;
  left: 55%;
}

.bubble:nth-child(6) {
  width: 45px;
  height: 45px;
  animation-duration: 8s;
  animation-delay: 3s;
  left: 65%;
}

.bubble:nth-child(7) {
  width: 25px;
  height: 25px;
  animation-duration: 7s;
  animation-delay: 2s;
  left: 75%;
}

.bubble:nth-child(8) {
  width: 80px;
  height: 80px;
  animation-duration: 6s;
  animation-delay: 1s;
  left: 80%;
}

.bubble:nth-child(9) {
  width: 15px;
  height: 15px;
  animation-duration: 9s;
  animation-delay: 0s;
  left: 70%;
}

.bubble:nth-child(10) {
  width: 50px;
  height: 50px;
  animation-duration: 5s;
  animation-delay: 3s;
  left: 85%;
}

@keyframes flying {
  0% {
    bottom: -100px;
    transform: translateX(0);
  }

  50% {
    transform: translateX(100px);
  }

  100% {
    bottom: 1080px;
    transform: translateX(-200px);
  }
}

